@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

/* Basic Construction */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

body {
    font-family: 'Montserrat', sans-serif;
    background-color: #586783;
    display: flex;
    justify-content: center;
    align-items: center;
}

.resume {
    margin: 10rem;
    display: grid;
    grid-template-columns: 1fr 2fr;
}

.resume .left-side {
    padding: 4rem;
    background-color: #323b4c;
    overflow: hidden;
}

.resume .right-side {
    padding: 4rem 3rem;
    background-color: #fff;
    overflow: hidden;
}

/* Reusable Components */
.padding-top-bg {
    padding-top: 4rem !important;
}

.padding-top-sm {
    padding-top: 2rem !important;
}

.heading-primary-white,
.heading-primary-black {
    font-size: 2.4rem;
    font-weight: bold;
    letter-spacing: 2px;
    margin-bottom: 1rem;
    position: relative;
}

.heading-primary-white { color: #fff; }
.heading-primary-black { color: #000; }

.heading-primary-white::after,
.heading-primary-black::after {
    content: '';
    height: 2px;
    position: absolute;
    left: 0;
    top: 3.5rem;
}

.heading-primary-white::after { background-color: #fff; width: 120%; }
.heading-primary-black::after { background-color: #000; width: 100%; }

.info-box .info-title {
    color: #fff;
    font-size: 1.8rem;
    font-weight: bold;
    margin-bottom: 5px;
}

.info-box .info,
.info-box .info a {
    color: #ddd;
    font-size: 1.4rem;
    text-decoration: none;
    margin-bottom: 5px;
}

.discription {
    font-size: 1.6rem;
    color: #555;
    padding: 1.5rem 0;
    text-align: justify;
    line-height: 1.5;
}

/* Profile */
.profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
}

.profile .name {
    font-size: 5.4rem;
    color: #333;
    margin-bottom: 1px;
}

.profile .title {
    font-size: 3.6rem;
    color: #555;
    letter-spacing: 3px;
    letter-spacing: 2px;
}

.profile .profile-img {
    width: 20rem;
    height: 20rem;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border: 2px solid #fff;
    margin-bottom: 2rem;
}

.resume .left-side .profile-img img {
    width: 100%;
}

/* Expertise */
.skill {
    display: flex;
    align-items: center;
    gap: 2rem;
}

.skill span {
    display: block;
    height: 3px;
    width: 3px;
    border-radius: 50%;
    background-color: #fff;
}

.skill .skill-name {
    color: #fff;
    font-size: 1.6rem;
}

/* Language */
.lang-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    padding-bottom: 1rem;
}

.lang-box p {
    width: 40%;
}

.lang-box .percent {
    position: relative;
    display: block;
    height: 5px;
    width: 60%;
    background-color: #fff;
}

.lang-box .percent div {
    position: absolute;
    background-color: #2264ff;
    height: 5px;
}

/* Experience */
.xp-box {
    position: relative;
    padding-left: 3rem;
    margin-bottom: 3rem;
}

.xp-box::before {
    content: '';
    position: absolute;
    top: 2rem;
    left: 0;
    background-color: #555;
    height: 100%;
    width: 2px;
}

.xp-box:last-child:before {
    height: 91%;
}

.xp-box::after {
    content: '';
    position: absolute;
    top: 2rem;
    left: -7px;
    background-color: #fff;
    height: 1.2rem;
    width: 1.2rem;
    border-radius: 50%;
    border: 2px solid #333;
}

.xp-box .xp-year {
    color: #333;
    font-size: 1.4rem;
    font-weight: bold;
    margin-bottom: 5px;
    letter-spacing: 2px;
}

.xp-box .xp-company {
    color: #555;
    font-size: 1.6rem;
    margin-bottom: 5px;
}

.xp-box .xp-position {
    color: #333;
    font-size: 1.8rem;
    font-weight: bold;
}

/* Reference */
.refer-box {
    display: flex;
    gap: 6rem;
}

.refer .refer-name {
    font-size: 2rem;
    margin-bottom: 3px;
}

.refer .job-position {
    color: #555;
    font-size: 1.6rem;
    margin-bottom: 8px;
}

.refer .contact {
    font-size: 1.4rem;
    color: #555;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.refer .contact span {
    display: block;
    width: 25%;
    color: #000;
    font-weight: bold;
}

.refer .contact p {
    width: 75%;
}

/* Responsive */
@media screen and (max-width: 1000px) {
    .resume {
        grid-template-columns: repeat(1, 1fr);
    }
}

@media screen and (max-width: 700px) {
    .refer-box { display: block; }
    .refer:first-child { margin-bottom: 3rem; }
    .resume { margin: 0; }
}
